/* Container for the form */
.form-container {
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* User form styling */
.user-form {
  display: flex;
  flex-direction: column;
}

/* Form group styling */
.form-group {
  margin-bottom: 15px;
}

.form-group label {
  font-weight: bold;
  margin-bottom: 5px;
}

.form-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* Product details styling */
.product-details {
  font-size: medium;
  margin-bottom: 20px;
  padding: 10px;
  background-color: #e0f7fa; /* Light blue background to highlight product info */
  border-radius: 4px;
  border: 1px solid #b2ebf2;
}

.product-details p {
  margin: 5px 0;
  font-size: 16px;
  color: #00796b; /* Darker teal color for text */
}

.product-details p strong {
  color: #004d40; /* Even darker for the bold text */
}

/* Styling for submit button */
.submit-button {
  background-color: #4caf50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.submit-button:hover {
  background-color: #45a049;
}

input[type="tel"],
input[type="email"],
input[type="text"],
input[type="number"] {
  font-size: 16px;
}
