/* Container for product detail */
.product-detail-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

/* Main section containing image and info side by side */
.product-detail-main {
  display: flex;
  gap: 20px;
  width: 100%;
  justify-content: center;
}

.product-detail-image {
  flex: 1;
  max-width: 500px;
}

.product-detail-info {
  flex: 2;
  text-align: left;
  max-width: 600px;
}

/* Styling for the details section, displayed below image and info */
.product-detail-extra-info {
  text-align: center;
  margin-top: 30px;
}

.details-section {
  margin: 0 auto;
  max-width: 700px;
}

/* Separated section for additional information, below details */
.additional-info-section {
  margin-top: 40px;
  text-align: center;
}

.additional-info-section h2 {
  font-size: 1.8rem;
  margin-bottom: 10px;
}

.additional-info {
  max-width: 700px;
  margin: 0 auto;
}

.additional-info p {
  margin: 5px 0;
}

/* Styling the buttons and inputs */
.quantity-controls {
  display: flex;
  align-items: center;
  gap: 5px;
}

.quantity-controls button {
  width: 35px;
  height: 35px;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  cursor: pointer;
}

.quantity-controls input {
  width: 60px;
  text-align: center;
  border: 1px solid #ccc;
  height: 35px;
}

.add-to-cart {
  margin-top: 20px;
}

.back-button {
  margin-top: 10px;
}

/* Media queries for smaller screens */
@media screen and (max-width: 800px) {
  .product-detail-main {
    flex-direction: column;
    align-items: center;
  }

  .product-detail-image {
    max-width: 100%;
    margin-bottom: 20px;
  }

  .product-detail-info {
    text-align: center;
  }
}
