.product-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 20px;
  padding: 20px;
}

/* Product card styling with image on top */
.product-card {
  display: flex;
  flex-direction: column; /* Stacks image and product info vertically */
  width: 100%; /* Full width for smaller screens */
  max-width: 300px; /* Max width for larger screens */
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 8px;
  transition: box-shadow 0.3s ease, transform 0.3s ease;
  cursor: pointer;
  align-items: center;
  text-decoration: none; /* Remove default underline from links */
  margin-bottom: 20px; /* Add margin between products */
}

.product-card:hover {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transform: translateY(-5px);
}

/* Image will take the full width of the card */
.product-img {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 8px 8px 0 0; /* Rounded corners at the top */
}

/* Product information styling */
.product-info {
  text-align: center;
  padding: 15px 0;
}

.product-info h2 {
  margin: 0 0 10px;
  font-size: 1.2rem;
}

.product-info p {
  margin: 0;
  color: #555;
  font-size: 0.9rem;
}

.product-price {
  font-size: 1rem;
  color: #333;
  font-weight: bold;
  margin-top: 10px;
}

/* Remove link styling for product links */
.product-link {
  text-decoration: none;
  color: inherit; /* Inherit color from parent */
}

/* Product detail container styling */
.product-detail-container {
  display: flex;
  flex-direction: row;
  gap: 20px;
  padding: 20px;
}

/* Product detail image styling */
.product-detail-image img {
  width: 400px;
  height: auto;
  border: 1px solid #ddd;
}

.product-detail-info {
  flex-grow: 1;
}

.product-detail-price {
  font-size: 1.5rem;
  font-weight: bold;
}

/* Add to cart button */
.add-to-cart {
  margin-left: 15px;
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #28a745; /* New green color for the button */
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-to-cart:hover {
  background-color: #218838; /* Darker green on hover */
}

/* Return to previous page button */
.back-button {
  margin-top: 20px;
  margin-left: 15px;
  padding: 10px 20px;
  background-color: #007bff; /* New blue color for the button */
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.back-button:hover {
  background-color: #0056b3; /* Darker blue on hover */
}

/* Extra product info styling */
.extra-info {
  margin-top: 20px;
  font-size: 0.9rem;
  color: #777;
}

/* Quantity controls styling */
.quantity-controls {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.quantity-controls button {
  width: 30px;
  height: 30px;
  font-size: 1.2rem;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.quantity-controls button:hover {
  background-color: #e0e0e0;
}

.quantity-controls input {
  width: 50px;
  text-align: center;
  font-size: 1rem;
  margin: 0 5px;
  border: 1px solid #ccc;
  height: 30px;
}

/* Responsive design */
@media screen and (max-width: 768px) {
  .product-container {
    justify-content: center;
  }

  .product-detail-container {
    flex-direction: column; /* Stack image and info vertically on small screens */
    align-items: center;
  }

  .product-detail-image img {
    width: 100%; /* Full width on small screens */
    max-width: 400px;
  }
}

@media screen and (max-width: 480px) {
  .product-info h2 {
    font-size: 1rem;
  }

  .product-info p {
    font-size: 0.8rem;
  }

  .product-price {
    font-size: 0.9rem;
  }

  .add-to-cart,
  .back-button {
    padding: 8px 16px;
    font-size: 0.9rem;
  }

  .product-card {
    padding: 10px;
  }

  .product-detail-image img {
    width: 100%;
  }

  .quantity-controls button {
    width: 25px;
    height: 25px;
    font-size: 1rem;
  }

  .quantity-controls input {
    width: 40px;
    font-size: 0.9rem;
  }
}
