.footer {
  background-color: #333; /* Light black */
  color: white;
  padding: 20px;
  text-align: center;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-links {
  list-style: none;
  padding: 0;
  display: flex;
  gap: 15px;
}

.footer-links li a {
  color: white;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-links li a:hover {
  color: #aaa; /* Light grey on hover */
}
