.header {
  width: 100%;
}

.image-container {
  position: relative;
  width: 100%;
  max-width: 100%; /* Ensures the container doesn't overflow */
  overflow: hidden; /* To ensure no overflow of content */
}

.main-img {
  width: 100%; /* Ensures the image takes the full width of the container */
  height: auto; /* Automatically adjusts height while keeping aspect ratio */
  display: block; /* Removes any space below the image */
}

.header-title {
  position: absolute;
  top: 13%; /* Centers text vertically */
  left: 50%; /* Centers text horizontally */
  transform: translate(-50%, -50%); /* Ensures text is perfectly centered */
  color: black;
  font-size: 3rem;
  font-weight: bold;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7); /* Enhances text readability */
  white-space: nowrap; /* Prevents text from breaking into multiple lines */
}

@media (max-width: 768px) {
  .header-title {
    font-size: 2rem; /* Reduces font size on smaller screens */
  }
}
